import { useCallback, useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { Col, Row } from 'antd';
import ProductCard from '../../../components/ProductCard';
import "./_style.scss";
import CardsTitleTop from '../../../components/CardsTitleTop';
import baseAPI from '../../../api/baseAPI';
import { recommendedProductsUrl } from '../../../api/apiUrls';
import { ProductType, RecommendedProductsResType } from '../../../types';

function Recommended() {

  const [recommendedProducts, setRecommendedProducts] = useState<ProductType[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const getNewProducts = useCallback(() => {
    baseAPI.fetchWithParams<RecommendedProductsResType>(recommendedProductsUrl, { limit: 10 })
      .then((res) => {
        if (res.data.status === 200) {
          setRecommendedProducts(res.data.data);
          setIsLoading(false);
        }
      })
  }, [])

  useEffect(() => {
    getNewProducts();
  }, [getNewProducts])
  return (
    <div className="recommended">
      <div className="container">
        <CardsTitleTop title="Рекомендуем" toUrl="#" />
        <Row gutter={[30, 30]}>
          <Col lg={6} md={0} sm={0} xs={0}>
            <div className="left">
              <Link to="#">
                <img className="recommended_img" src="/assets/img/recommended.png" alt="recommended" />
              </Link>
            </div>
          </Col>
          <Col lg={18} md={24}>
            <Row gutter={[30, 30]}>
              {
                recommendedProducts.map(recommendedProduct => (
                  <Col lg={6} md={8} sm={12} xs={24} key={recommendedProduct.id}>
                    <ProductCard {...recommendedProduct} />
                  </Col>
                ))
              }
            </Row>
          </Col>
        </Row>
      </div>
    </div>
  )
}

export default Recommended